  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  background-color: #141414;
  color: white;
  font-family: sans-serif;
}

.Grid {
  display: grid;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  padding: 1rem;
  justify-items: center;
}
@media screen and (max-width: 1100px) {
  .Grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1100px) {
  .Grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.back {
  background: black;
  opacity: 0.7;
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  top: 0;
  left: 0;
}

.modal_back {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  top: 0;
  left: 0;
}

.hidden {
  display: none;
}

.modal {
  background: white;
  color: black;
  opacity: 1;
  width: auto;
  max-width: 90%;
  transition: 1.1s ease-out;
  box-shadow: -2rem 2rem 2rem rgba(black, 0.2);
  -webkit-filter: blur(0);
          filter: blur(0);
  transform: scale(1);
  visibility: visible;
}

