body {
  font-family: 'Roboto', Arial, Sans;
  padding: 1.6rem;
  color: #555;
}

h1 {
  font-size: 1.8rem;
  margin-top: 0.3rem;
  margin-bottom: 3.5rem;
  padding-bottom: 1.5rem;
  font-weight: normal;
  border-bottom: 1px solid #c5c5c5;
  font-weight: 300;
}

input {
  font-size: 1rem;
  padding: 0.5rem;
  border: 1px solid #c5c5c5;
  color: #555;
}

span {
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.3rem;
}

.color-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #a1a1a1;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}

.hexcode {
  display: block;
  font-size: 0.8rem;
  position: absolute;
  top: -1.6rem;
  left: -0.6rem;
  height: 4rem;
  min-width: 10rem;
  padding: 0.4rem 0.4rem 0.4rem 0.6rem;
  color: #fff;
  margin-right: 0;
}

.sliders {
  margin-top: 1rem;
}

.sliders label {
  display: block;
  margin-top: 1.5rem;
}

.slider {
  margin-top: 1rem;
  width: 100%;
  display: inline-block;
}

.ui-slider .custom-handle {
  width: 2.5em;
  height: 1.6em;
  top: 50%;
  margin-top: -0.8em;
  text-align: center;
  line-height: 1.6em;
}

.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 600px;
}
.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #000;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

